<template>
  <div class="container">
    <div class="box">
      <h2>Todo List</h2>
      <input
        v-model="inpText"
        @keydown.enter="addtoTask"
        type="text"
        placeholder="Write here ..."
      />
      <ul class="list">
        <li
          class="list__li"
          v-for="item in list"
          :key="item.id"
          :class="{ active: item.done }"
          @click="item.done = !item.done"
        >
          {{ item.title }} <i @click="removeText(item)"></i>
        </li>
      </ul>
      <div class="overall-task">
        Pending Task:
        <span class="overall-task-progress">{{ pendingTasks }}</span>
        <button class="overall__btn" @click="list.length = 0">Del All</button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      inpText: "",
      active: false,
    };
  },
  props: {
    list: {
      typeof: Array,
    },
  },
  methods: {
    addtoTask() {
      if (this.inpText != 0) {
        this.list.push({
          id: new Date().getTime(),
          title: this.inpText,
          done: false,
        });
        this.inpText = "";
      } else {
        alert("Iltmos birinch bolib yozib oling !!! âœ’ ðŸ”‘");
      }
    },
    removeText(item) {
      const index = this.list.indexOf(item);
      // console.log(index);
      // if (index !== -1) {
      this.list.splice(index, 1);
      // }
    },
  },
  computed: {
    pendingTasks() {
      return this.list.filter((item) => item.done == false).length;
    },
  },
};
</script>

<style lang="scss"></style>
